@import url(https://fonts.googleapis.com/css?family=Anonymous+Pro);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.main-container{width:100%;height:100%;color:#fff;display:flex;flex-direction:column;justify-content:center;align-items:center}.typewriter-text{overflow:hidden;position:relative;width:18em;border-right:2px solid rgba(255,255,255,0.75);font-size:38px;white-space:nowrap;margin-top:100px;-webkit-animation:typewriter 4s steps(35) 1s 1 normal both,blinkTextCursor 800ms steps(30) infinite normal;animation:typewriter 4s steps(35) 1s 1 normal both,blinkTextCursor 800ms steps(30) infinite normal}.intro-summary{font-size:25px;margin-top:10px;text-align:center;margin-bottom:25px}@-webkit-keyframes typewriter{from{width:0}to{width:9em}}@keyframes typewriter{from{width:0}to{width:9em}}@-webkit-keyframes blinkTextCursor{from{border-right-color:rgba(255,255,255,0.75)}to{border-right-color:transparent}}@keyframes blinkTextCursor{from{border-right-color:rgba(255,255,255,0.75)}to{border-right-color:transparent}}

.subtitle{color:#bb86fc}hr{color:#03dac5;border:none;background-color:#03dac5}

html{width:100%;height:100%}body{width:100%;height:100%;font-family:"Anonymous Pro", sans-serif;background-color:#121212;color:#fff}body::-webkit-scrollbar{display:none}body{-ms-overflow-style:none;scrollbar-width:none}a{text-decoration:none}

